<template>
    <div class="d-flex">

        <div class="flex-shrink-0 p-3 bg-white left-nav">
            <div class="d-flex align-items-center pb-3 mb-3 link-dark text-decoration-none border-bottom">
                <i class="bi bi-shop me-md-3"></i>
                <span class="fs-5 fw-semibold">Products</span>
            </div>

            <ul class="list-unstyled ps-0">
                <li class="mb-1">
                    <button class="btn" @click="visibleHandler">
                        <i v-if="currentNav === 'Christmas'" class="bi bi-chevron-down"></i>
                        <i v-else class="bi bi-chevron-right"></i>
                        Christmas
                    </button>
                    <!-- Christmas sub menu -->
                    <ul class="btn-toggle-nav list-unstyled fw-normal pb-1 small" v-if="currentNav === 'Christmas'">
                        <li @click="filterItems('Branch')">
                            <a class="link-dark rounded">Branches & Leaves</a>
                        </li>
                        <li @click="filterItems('Circle')">
                            <a class="link-dark rounded">Circle</a>
                        </li>
                        <li @click="filterItems('Long Vine')">
                            <a class="link-dark rounded">Long Vine</a>
                        </li>
                        <li @click="filterItems('Water Drop')">
                            <a class="link-dark rounded">Water Drop</a>
                        </li>
                        <li @click="filterItems('Lintel')">
                            <a class="link-dark rounded">Lintel</a>
                        </li>
                        <li @click="filterItems('Candlestick')">
                            <a class="link-dark rounded">Candlestick</a>
                        </li>
                        <li @click="filterItems('Potted')">
                            <a class="link-dark rounded">Potted</a>        
                        </li>
                        <li @click="filterItems('Others')">
                            <a class="link-dark rounded">Others</a>
                        </li>

                    </ul>
                </li>

                <li class="mb-1">
                    <button class="btn btn-toggle align-items-center rounded collapsed" @click="visibleHandler">
                        <i v-if="currentNav === 'Spring'" class="bi bi-chevron-down"></i>
                        <i v-else class="bi bi-chevron-right"></i>
                        Spring
                    </button>
                    <!-- Spring sub menu -->
                    <ul class="btn-toggle-nav list-unstyled fw-normal pb-1 small" v-if="currentNav === 'Spring'">
                        <li @click="filterItems('Floral')">
                            <a class="link-dark rounded">Floral</a>
                        </li>
                        <li @click="filterItems('Foliage')">
                            <a class="link-dark rounded">Foliage</a>
                        </li>
                        <li @click="filterItems('Potted')">
                            <a class="link-dark rounded">Potted</a>
                        </li>
                        <li @click="filterItems('Fruit')">
                            <a class="link-dark rounded">Fruit & Berries</a>
                        </li>
                        <li>
                            <a class="link-dark rounded">Others</a>
                        </li>
                    </ul>
                </li>

                <li class="mb-1" @click="visibleHandler">
                    <button class="btn btn-toggle align-items-center rounded collapsed">
                        <i class="bi bi-chevron-right"></i>
                        Popular products
                    </button> 
                </li>
            </ul>
        </div>

        <!-- products -->
        <div class="row">

            <div class="col-md-6 d-flex border" v-for="(item,index) in filteredProducts" :key="index" v-show="item.Name === '23 Cypress Branch'">
                <div class="my-md-5">
                    <img ref="productImage" :src="item.Image" class="img-thumbnail product-img">
                </div>
                
                <div class="mx-md-5 mt-md-5">
                    <h5>{{ item.Name }}</h5>
                    <p>Item No: {{ item.ItemNo }}</p>
                    <div> 
                        <div class="option" :style="{ backgroundImage: `url(${colour}`}" v-for="(colour,index) in item.Colour" @click="selectHandler(colour,item)"></div>
                    </div>
                </div>

            </div>

        </div>

    </div>
</template>

<script>
import { thisTypeAnnotation } from '@babel/types'
import axios from 'axios'

    export default{
        name: 'ProductsView',
        data() {
            return {
                currentNav: '',
                currentImage: 'https://static.runoob.com/images/mix/img_fjords_wide.jpg',
                optionImage: 'https://cdn.pixabay.com/photo/2023/11/14/15/46/nikon-8388022_1280.jpg',
                products: [],
                selectedFilter: ''
            }
        },
        methods: {
            visibleHandler(event) {
                this.currentNav = event.target.innerText.trim()
            },
            selectHandler(imgUrl,currentItem) {
                currentItem.Image = imgUrl
                const imgElement = this.$refs.productImage
                imgElement.src = imgUrl
            },
            filterItems(value) {
                this.selectedFilter = value
                console.log(value)
            }
        },
        mounted() {
            axios.get('http://www.china-fix.com:3000/api/data').then(res => {
                this.products = res.data
            })
        },
        computed: {
            filteredProducts() {
                if (this.selectedFilter) {
                    return this.products.filter(item => item.Name.includes(this.selectedFilter))
                } else {
                    return this.products
                }
            }
        }
    }
</script>


<style>
/* Mobile Phone */
@media screen and (max-width: 768px) {}

/* Ipad */
@media screen and (min-width: 768px) {}

/* Desktop */
@media screen and (min-width: 992px) {
    .left-nav {
        width: 280px;
    }

    .btn-toggle-nav a {
        display: inline-flex;
        padding: 0.1875rem 0.5rem;
        margin-top: 0.125rem;
        margin-left: 1.75rem;
        text-decoration: none;
    }

    .link-dark:hover{
        color: #FC6800 !important;
    }

    .option{
        width: 36px;
        height: 36px;
        margin: 20px 20px 20px 0;
        background-size: 100% 100%;
        border: 1px solid #dee2e6;
        display: inline-block;
    }

    .product-img{
        width: 150px;
        height: 200px;
        margin-left: 20px;
    }
}
</style>